* {
    --detail-color: #83781B;
    --detail-background-color: #4B4737;
    --component-background-color: #3D4C1F;
    --main-background-color: #B0AB99;
    --hoover-color: #008B77;
    --active-color: #00C2AB;
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: padding-box;
    font-size: 16px;
    vertical-align: baseline;
    background: transparent;
    color: var(--detail-color);
}

a {
    color: var(--detail-color)
}

a:hover {
    color: var(--hoover-color);
}

.solid-border {
    background-color: var(--component-background-color);
}

.app-conteiner {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: 100vh;
    background-color: var(--main-background-color);
}

.app-content {
    display: flex;
    width: 70%;
    max-width: 800px;
    min-width: 320px;
    height: 100%;
    min-height: 500px;
    background-color: var(--detail-background-color);
}

.content {
    position: relative;
    margin: 0.5rem 0.5rem 0 0.5rem;
    top: -1rem;
    display: grid;
    grid-template-columns: auto;
    grid-template-rows: 6.5rem 3rem 1fr;
    justify-items: center;
    border: 1px var(--detail-color) solid;
    background-color: var(--component-background-color);
}

.content-conteiner {
    width: 100%;
    max-width: 800px;
    margin: 0.5rem auto;
    display: grid;
    grid-template-columns: auto;
    grid-template-rows: auto 1fr 3rem;
    position: relative;
    right: 1rem;
}

.nav-bar-cont {
    width: 90%;
    margin: 0 auto;
}

table {
    border: 4px solid var(--detail-color);
    margin: 0 auto;
}

.table-conteiner {
    margin: 0 0.2rem;
}

.center {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
}

th {
    background-color: var(--detail-color);
    color: var(--component-background-color);
    vertical-align: center;
}

td {
    border: 1px solid var(--detail-color);
    font-size: 1rem;
    height: 2rem;
    padding: 0.4rem;
    vertical-align: none;
}

.stars {
    width: 100%;
    height: 100%;
    justify-content: center;
}

.link {
    justify-content: center;
}

.box {
    width: 100%;
    display: flex;
    align-items: flex-start;
    justify-content: center;
}

@media (max-width: 720px) {
    .app-content {
        width: 100%;
    }
}

@media (max-width: 400px) {
    .content {
        grid-template-rows: 7.5rem 3rem 1fr;
        text-align: center;
        justify-items: center;
        border: 1px var(--detail-color) solid;
        background-color: var(--component-background-color);
    }  
}
